@import '~nav-frontend-core/less/hide-text';
@import '~nav-frontend-core/less/_mixins';
@import '~nav-frontend-core/less/utilities';
@import '/src/globals.module';

.header {
	background-color: white;
	padding: 2rem 3rem 2rem 3rem;
}

.headerContent {
	display: flex;
	flex-direction: row;
	align-items: center;

	max-width: @max-bredde;
	margin: 0 auto;
}

.headerTitle {
	margin-right: 2rem;
}

.tilbakeknapp {
	display: block;
	margin-right: 2rem;
	text-decoration: none;
}

.detaljer {
	padding: 2rem;
	max-width: @max-bredde;
	margin: 0 auto;
}

.tiltakCard {
	width: 100%;
	margin-bottom: 2rem;
}

.userInfoContent {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-column-gap: 2rem;
	grid-row-gap: 2rem;
}
