@import '~nav-frontend-core/less/_variabler';

.wrapper {
  position: relative;
}

.kopierKnapp {
  padding: 0.5rem;
  font-size: 1rem;
  line-height: 1rem;
  color: @navMorkGra;
  border: none;
}

.tooltip {
  position: absolute;
  bottom: 50px;
  padding: 0.5rem;
  opacity: 0;

  display: none;
  border-radius: 4px;
  background: white;
  border: 1px solid @navMorkGra;
  color: @navMorkGra;
  font-size: 1rem;

  &:after {
    top: 100%;
    left: 50%;
    border: 5px solid rgba(51, 51, 51, 0);
    content: ' ';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-top-color: @navMorkGra;
    margin-left: -5px;
  }
}

.tooltipVisible {
  display: block;
  z-index: 100;
  opacity: 1;
}
